}
#art .artList3 img {
  max-width: 100%;
  width: 300px;
  height: 400px;
  object-fit: cover;
  outline: 4px solid black;
}
.artList1 {
  text-align: center;
  padding: 15px;
  text-align: center;
  background-color: #bef38c;
  background-image: url("/public/images/rustyOverlay.png");
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(54, 51, 51);
  border: 3px solid black;
}
.artList2 {
  text-align: center;
  padding: 15px;
  text-align: center;
  background-color: #fde610;
  background-image: url("/public/images/rustyOverlay.png");
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(54, 51, 51);
  border: 3px solid black;
}
.artList3 {
  text-align: center;
  padding: 15px;
  text-align: center;
  background-color: #db61f3;
  background-image: url("/public/images/rustyOverlay.png");
  background-size: cover;
  background-repeat: no-repeat;
  color: rgb(54, 51, 51);
  border: 3px solid black;
}

#allList img:hover {
  cursor: pointer;
  -webkit-filter: brightness(150%);
}

#art h1 img {
  margin-bottom: 50px;
}
#art .artSection a {
  width: 100%;
  background-color: rgb(252, 145, 5);
  text-decoration: none;
  margin-top: 4px;
  color: black;
  padding: 12px 12px;
  cursor: pointer;
  text-transform: uppercase;
  border: 1px solid transparent;
  transition: background-color 1s ease-in-out;
}

#art .artSection a:hover {
  background-color: white;
  border-color: black;
}

#art p {
  background-color: rgb(141, 140, 140);
  color: white;
  background-image: url("/public/images/rustyOverlay.png");
  border: 1px solid rgb(27, 26, 26);
}
#art span {
  font-size: 2rem;
  color: rgb(122, 198, 248);
}
#art .closeModalBtn {
  background-color: transparent;
  border: transparent;
  position: fixed;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.model {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;

  visibility: hidden;
  opacity: 0;
  transform: scale(0);
  overflow: hidden;
  z-index: 999;
}

.model.open {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}

.model img {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 20px 0 20px;
  margin: 0 auto;
}

.model.open .closeModal {
  position: fixed;
  top: 10px;
  right: 10px;
  width: 4rem;
  height: 4rem;
  padding: 5px;
  cursor: pointer;
}

#art .artSection .priceContainer {
  margin-top: 25px;
}
#art .artSection .priceContainer p {
  margin-top: 25px;
}

@media (min-width: 1024px) {
  #art .artList1,
  .artList2,
  .artList3 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 25px;
  }
}

@media (max-width: 1024px) {
  #art .artList1,
  .artList2,
  .artList3 {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 25px;
  }
  #art .artSection h3 {
    font-size: 13px;
  }
}

@media (max-width: 560px) {
  #art .artList1,
  .artList2,
  .artList3 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 25px;
  }
}
